* {
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding: 60px;
}

input{
  width: 100%;
  font-size: 1.1rem;
  color: grey;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 6px;
  margin-right: 6px;
  overflow: hidden;
}

.card p{
  padding:5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}